// MATRIX CODE RAIN BACKGROUND COMPONENT
const MatrixCodeRain = () => {
  useEffect(() => {
    const createMatrixRain = () => {
      const canvas = document.getElementById('matrix-canvas');
      if (!canvas) return;
      
      const ctx = canvas.getContext('2d');
      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;

      const chars = "019SOLUTIONSABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789@#$%^&*()";
      const charArray = chars.split("");
      const font_size = 14;
      const columns = canvas.width / font_size;
      const drops = [];

      for (let x = 0; x < columns; x++) {
        drops[x] = 1;
      }

      const draw = () => {
        ctx.fillStyle = 'rgba(10, 10, 15, 0.05)';
        ctx.fillRect(0, 0, canvas.width, canvas.height);
        
        ctx.fillStyle = '#00d4ff';
        ctx.font = font_size + 'px JetBrains Mono';
        
        for (let i = 0; i < drops.length; i++) {
          const text = charArray[Math.floor(Math.random() * charArray.length)];
          ctx.fillStyle = i % 3 === 0 ? '#ff0080' : (i % 2 === 0 ? '#8a2be2' : '#00d4ff');
          ctx.fillText(text, i * font_size, drops[i] * font_size);
          
          if (drops[i] * font_size > canvas.height && Math.random() > 0.975) {
            drops[i] = 0;
          }
          drops[i]++;
        }
      };

      const interval = setInterval(draw, 50);
      return () => clearInterval(interval);
    };

    const cleanup = createMatrixRain();
    
    const handleResize = () => {
      const canvas = document.getElementById('matrix-canvas');
      if (canvas) {
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;
      }
    };

    window.addEventListener('resize', handleResize);
    
    return () => {
      if (cleanup) cleanup();
      window.removeEventListener('resize', handleResize);
    };
  }, []);

  return (
    <canvas 
      id="matrix-canvas" 
      className="matrix-background"
      style={{
        position: 'fixed',
        top: 0,
        left: 0,
        width: '100%',
        height: '100%',
        zIndex: -1,
        opacity: 0.3
      }}
    />
  );
};

