// MODERN NAVIGATION WITH HOVER EFFECTS
const ModernNavigation = () => {
  const { t } = useTranslation();
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [isMenuHovered, setIsMenuHovered] = useState(false);
  const menuRef = useRef(null);

  const navItems = [
    { href: '#hero', label: 'Home', icon: '' },
    { href: '#services', label: 'Services', icon: '' },
    { href: '#portfolio', label: 'Portfolio', icon: '' },
    { href: '#admin', label: 'Admin', icon: '' },
    { href: '#search', label: 'Search', icon: '' },
    { href: '#contact', label: 'Contact', icon: '' }
  ];

  const handleMenuItemClick = (href) => {
    setIsMenuOpen(false);
    setTimeout(() => {
      document.querySelector(href)?.scrollIntoView({ behavior: 'smooth' });
    }, 100);
  };

  return (
    <nav className="modern-nav">
      <div className="nav-container">
        <div className="nav-brand">
          <span className="brand-logo">019</span>
          <span className="brand-text">SOLUTIONS</span>
        </div>
        
        <div 
          className="burger-container"
          onMouseEnter={() => setIsMenuHovered(true)}
          onMouseLeave={() => setIsMenuHovered(false)}
        >
          <div className={`burger-menu ${isMenuHovered || isMenuOpen ? 'active' : ''}`}>
            <div className="burger-line"></div>
            <div className="burger-line"></div>
            <div className="burger-line"></div>
          </div>
          
          <div 
            ref={menuRef}
            className={`nav-menu ${isMenuHovered || isMenuOpen ? 'active' : ''}`}
          >
            <div className="menu-header">
              <h3>Navigation</h3>
              <LanguageSwitcher />
            </div>
            
            <div className="menu-items">
              {navItems.map((item, index) => (
                <button 
                  key={index}
                  onClick={() => handleMenuItemClick(item.href)}
                  className="nav-item"
                >
                  <span className="nav-icon">{item.icon}</span>
                  <span className="nav-label">{item.label}</span>
                </button>
              ))}
            </div>
          </div>
        </div>
      </div>
    </nav>
  );
};

