import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useTranslation } from 'react-i18next';

const API = process.env.REACT_APP_BACKEND_URL;

// Modern Admin Panel Component
const AdminPanel = () => {
  const { t } = useTranslation();
  const [activeTab, setActiveTab] = useState('dashboard');
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [adminCredentials, setAdminCredentials] = useState({ username: '', password: '' });
  const [stats, setStats] = useState({
    totalContacts: 0,
    totalProjects: 0,
    totalServices: 0,
    totalNotifications: 0
  });

  // Authentication
  const handleLogin = (e) => {
    e.preventDefault();
    // Simple admin authentication (in production, use proper JWT)
    if (adminCredentials.username === 'admin' && adminCredentials.password === '019solutions2025') {
      setIsAuthenticated(true);
      loadAdminStats();
    } else {
      alert('Invalid credentials');
    }
  };

  const loadAdminStats = async () => {
    try {
      const [contacts, projects, services, notifications] = await Promise.all([
        axios.get(`${API}/api/contact`),
        axios.get(`${API}/api/projects`),
        axios.get(`${API}/api/services`),
        axios.get(`${API}/api/notifications/admin@019solutions.com`)
      ]);

      setStats({
        totalContacts: contacts.data?.length || 0,
        totalProjects: projects.data?.length || 0,
        totalServices: services.data?.length || 0,
        totalNotifications: notifications.data?.length || 0
      });
    } catch (error) {
      console.error('Error loading admin stats:', error);
    }
  };

  // Modern Login Form
  if (!isAuthenticated) {
    return (
      <section id="admin" className="modern-admin-section">
        <div className="container">
          <div className="modern-admin-login">
            <div className="modern-login-card">
              <div className="modern-login-header">
                <h2> Admin Panel</h2>
                <p>019 Solutions Management Dashboard</p>
              </div>
              <form onSubmit={handleLogin} className="modern-login-form">
                <div className="modern-input-group">
                  <input
                    type="text"
                    placeholder="Username"
                    value={adminCredentials.username}
                    onChange={(e) => setAdminCredentials({...adminCredentials, username: e.target.value})}
                    className="modern-input"
                    required
                  />
                  <span className="modern-input-icon"></span>
                </div>
                <div className="modern-input-group">
                  <input
                    type="password"
                    placeholder="Password"
                    value={adminCredentials.password}
                    onChange={(e) => setAdminCredentials({...adminCredentials, password: e.target.value})}
                    className="modern-input"
                    required
                  />
                  <span className="modern-input-icon"></span>
                </div>
                <button type="submit" className="modern-login-btn">
                  Access Admin Panel
                </button>
              </form>
            </div>
          </div>
        </div>
      </section>
    );
  }

  // Modern Admin Dashboard
  return (
    <section id="admin" className="modern-admin-section">
      <div className="container">
        <div className="modern-admin-header">
          <div className="admin-title-section">
            <h2> 019 Solutions Admin Panel</h2>
            <p>Comprehensive website management and analytics dashboard</p>
          </div>
          <div className="admin-user-section">
            <span className="admin-user"> Admin</span>
            <button 
              className="logout-btn"
              onClick={() => setIsAuthenticated(false)}
            >
               Logout
            </button>
          </div>
        </div>

        <div className="modern-admin-tabs">
          <button 
            className={`modern-tab ${activeTab === 'dashboard' ? 'active' : ''}`}
            onClick={() => setActiveTab('dashboard')}
          >
             Dashboard
          </button>
          <button 
            className={`modern-tab ${activeTab === 'content' ? 'active' : ''}`}
            onClick={() => setActiveTab('content')}
          >
             Content
          </button>
          <button 
            className={`modern-tab ${activeTab === 'analytics' ? 'active' : ''}`}
            onClick={() => setActiveTab('analytics')}
          >
             Analytics
          </button>
          <button 
            className={`modern-tab ${activeTab === 'settings' ? 'active' : ''}`}
            onClick={() => setActiveTab('settings')}
          >
             Settings
          </button>
        </div>

        <div className="modern-admin-content">
          {activeTab === 'dashboard' && <AdminDashboard stats={stats} />}
          {activeTab === 'content' && <ContentManagement />}
          {activeTab === 'analytics' && <Analytics />}
          {activeTab === 'settings' && <AdminSettings />}
        </div>
      </div>
    </section>
  );
};

