// Content Management Component
const ContentManagement = () => {
  const [services, setServices] = useState([]);
  const [projects, setProjects] = useState([]);

  useEffect(() => {
    loadServices();
    loadProjects();
  }, []);

  const loadServices = async () => {
    try {
      const response = await axios.get(`${API}/api/services`);
      setServices(response.data);
    } catch (error) {
      console.error('Error loading services:', error);
    }
  };

  const loadProjects = async () => {
    try {
      const response = await axios.get(`${API}/api/projects`);
      setProjects(response.data);
    } catch (error) {
      console.error('Error loading projects:', error);
    }
  };

  return (
    <div className="content-management">
      <div className="content-section">
        <h3>Services Management</h3>
        <div className="content-grid">
          {services.map((service, index) => (
            <div key={service.id || index} className="content-card glass">
              <h4>{service.name}</h4>
              <p>{service.description?.substring(0, 100)}...</p>
              <div className="content-actions">
                <button className="btn-secondary">Edit</button>
                <button className="btn-primary">Delete</button>
              </div>
            </div>
          ))}
        </div>
      </div>

      <div className="content-section">
        <h3>Portfolio Management</h3>
        <div className="content-grid">
          {projects.map((project, index) => (
            <div key={project.id || index} className="content-card glass">
              <h4>{project.name}</h4>
              <p>{project.description?.substring(0, 100)}...</p>
              <div className="content-actions">
                <button className="btn-secondary">Edit</button>
                <button className="btn-primary">Delete</button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

